.categories__container {
  height: 228px;
  overflow: scroll;
}

.categories__list {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.categories__link {
  color: rgba(17, 17, 17, .6);
  letter-spacing: -.32px;
  font-size: 16px;
  line-height: 1.125;
}

.container {
  max-width: 375px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.books-title {
  color: #111;
  letter-spacing: -1.28px;
  font-size: 32px;
  line-height: 1.18;
}

.last-word {
  color: #4f2ee8;
}

.books-category-name {
  color: #b4afaf;
  letter-spacing: .42px;
  text-transform: uppercase;
  margin-top: 40px;
  margin-bottom: 18px;
  font-size: 14px;
  line-height: 1.285;
}

.img-wrapper {
  width: 335px;
  height: 485px;
  border-radius: 8px;
  margin-bottom: 14px;
  overflow: hidden;
}

.book-img {
  width: 100%;
  height: 100%;
}

.book-title {
  color: #111;
  letter-spacing: -.64px;
  text-transform: uppercase;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
}

.book-author {
  color: #b4afaf;
  letter-spacing: -.48px;
  font-size: 12px;
  font-style: italic;
  line-height: 1.166;
}

.see-more-btn {
  color: #111;
  letter-spacing: -.14px;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #4f2ee8;
  border-radius: 40px;
  margin-top: 8px;
  margin-left: auto;
  margin-right: 0;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.285;
  display: block;
}

.top-books-category-list li:nth-last-child(-n+4) {
  display: none;
}

.books-category-list .book-card {
  margin-top: 40px;
}









@media screen and (min-width: 768px) {
  .container {
    max-width: 744px;
  }

  .category-list {
    flex-wrap: wrap;
    column-gap: 25px;
    display: flex;
  }

  .book-card {
    width: calc(33.3333% - 16.6667px);
  }

  .img-wrapper {
    width: 100%;
    height: 316px;
  }

  .top-books-category-list li:nth-child(-n+3) {
    display: list-item;
  }
}

@media screen and (min-width: 1440px) {
  .top-books-category-list li.book-card {
    display: list-item;
  }

  .page-wrapper {
    justify-content: center;
    display: flex;
  }

  .container {
    max-width: 1016px;
    margin-left: 40px;
    padding-left: 0;
  }

  .category-list {
    column-gap: 24px;
  }

  .book-card {
    width: calc(20% - 19.2px);
  }

  .img-wrapper {
    width: 100%;
    height: 256px;
  }
}



